@mixin css
{
    placeholder-designer
    {
        >div.placeholder
        {
            background-image: repeating-linear-gradient(-45deg, var(--dynamic-placeholder-background), var(--dynamic-placeholder-background) 10px, var(--dynamic-placeholder-foreground) 12px, var(--dynamic-placeholder-foreground) 12px);
            height: 26px;
        }
    }
}